r-radius: 10px\n    outline: 3px solid transparent\n    border: none\n    text-indent: 5px\n    font-size: 1.5rem\n    width: 250px\n    height: 40px\n\n  input:hover\n    outline: 3px solid $c-nav\n    border: none\n\n  input:focus\n    outline: 3px solid $c-nav\n    border: none\n\n  button\n    background-color: transparent\n    border: none\n    font-size: 2rem\n\n.todo-list\n  display: flex\n  flex-direction: column\n  gap: 25px\n  align-items: center\n  background-color: $bgc-todo-list\n  color: $c-todo-list\n\n  .todo-header\n    width: 100%\n    display: flex\n    flex-direction: column\n    align-items: center\n    justify-content: space-around\n\n  h2\n    font-weight: $fw-bold\n    font-size: 4rem\n\n  p\n    font-size: 1.5rem\n\n  .todo-body\n    width: 100%\n    display: flex\n    flex-direction: column\n    align-items: left\n    gap: 15px\n    padding: 30px\n\n    .tasks\n\n      display: flex\n      flex-direction: column\n      gap: 15px\n\n      .task\n        position: relative\n        width: fit-content\n        display: flex\n        gap: 15px\n        align-items: center\n\n        font-size: 2rem\n\n        input[type=\"checkbox\"]\n          /* Add if not using autoprefixer */\n          -webkit-appearance: none\n          /* Remove most all native input styles */\n          appearance: none\n          /* For iOS < 15 */\n          background-color: white\n          /* Not removed via appearance */\n          margin: 0\n\n          font: inherit\n          color: currentColor\n          width: 1.15em\n          height: 1.15em\n          border: 0.15em solid currentColor\n          border-radius: 0.15em\n\n          display: grid\n          place-content: center\n\n        input[type=\"checkbox\"]::before\n          content: \"\"\n          width: 0.65em\n          height: 0.65em\n          clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%)\n          transform: scale(0)\n          transform-origin: bottom left\n          transition: 120ms transform ease-in-out\n          box-shadow: inset 1em 1em purple\n          /* Windows High Contrast Mode */\n          background-color: CanvasText\n\n        input[type=\"checkbox\"]:checked::before\n          transform: scale(1)\n\n        label\n          display: inline-flex\n          align-items: center\n          position: relative\n\n        label::after\n          content: \"\"\n          position: absolute\n          left: 0\n          right: 0\n          left: 0em\n          top: 50%\n          height: 3px\n          background: currentColor\n          transform: scaleX(0)\n          transform-origin: right\n          transition: transform 150ms ease-in-out\n\n        [type=\"checkbox\"]:checked + label\n          opacity: 0.5\n\n        [type=\"checkbox\"]:checked + label::after\n          transform: scaleX(1)\n          transform-origin: left\n\n      .task:hover, input:hover, label:hover\n        cursor: pointer\n\n    .task-management-container\n      padding: 15px 20px\n      margin-bottom: 25px\n      border-radius: 10px\n      display: flex\n      flex-direction: column\n      align-self: center\n      gap: 5px\n      background-color: $bgc-task-management-container\n      color: $c-task-management-container\n\n      .new-task-creator\n        input\n          border-radius: 10px\n          outline: 3px solid transparent\n          border: none\n          text-indent: 5px\n          font-size: 1.5rem\n          width: 270px\n          height: 40px\n\n        input:hover\n          outline: 3px solid black\n          border: none\n\n        input:focus\n          outline: 3px solid black\n          border: none\n\n        button\n          background-color: transparent\n          border: none\n          font-size: 2rem\n\n      .delete-stuff\n\n        padding-left: 20px\n        display: flex\n        gap: 15px\n\n        button\n          color: $c-task-management-container\n          padding: 5px\n          font-size: 1rem\n 